body {
  min-height: 100vh;
  background-image: url("passion_bg.2762159f.jpg");
  background-position: 45% 25%;
  font-family: Karla, sans-serif;
}

@media (max-width: 767px) {
  body {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

.login-card {
  width: 441px;
  background-color: #fff;
  border: 0;
  border-radius: 8px;
}

@media (max-width: 767px) {
  .login-card {
    width: auto;
  }
}

.login-card .card-body {
  padding: 55px 55px 50px;
}

.login-card-title {
  text-align: center;
  color: #000;
  margin-bottom: 13px;
  font-size: 25px;
  font-weight: bold;
}

.login-card-description {
  color: #000;
  text-align: center;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: normal;
}

.login-card .form-control {
  min-height: 48px;
  border: 1px solid #f5f5f5;
  border-radius: 0;
  margin-bottom: 18px;
  padding: 16px 18px;
  font-size: 14px;
  font-weight: normal;
}

.login-card .form-control::-webkit-input-placeholder {
  color: #b0adad;
}

.login-card .form-control::-moz-placeholder {
  color: #b0adad;
}

.login-card .form-control:-ms-input-placeholder {
  color: #b0adad;
}

.login-card .form-control::placeholder {
  color: #b0adad;
}

.login-card-check-box {
  padding-left: 34px;
}

@media (max-width: 767px) {
  .login-card-check-box {
    margin-bottom: 10px;
  }
}

.login-card-check-box .custom-control-label:before {
  width: 24px;
  height: 21px;
  border: 1px solid #f5f5f5;
  top: 0;
  left: -34px;
}

.login-card-check-box .custom-control-label:after {
  width: 24px;
  height: 21px;
  top: 0;
  left: -34px;
}

.login-card .login-btn {
  color: #fff;
  background-color: #000;
  border-radius: 0;
  margin-bottom: 36px;
  padding: 13px 20px;
  font-size: 20px;
  font-weight: bold;
}

.login-card .login-btn:hover {
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
}

.login-card-footer-text {
  color: #000;
  text-align: center;
  margin-bottom: 0;
  font-size: 13px;
}

.form-options-wrapper {
  color: #000;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: normal;
  display: flex;
}

@media (max-width: 767px) {
  .form-options-wrapper {
    display: block;
  }
}

.footer-content-wrapper {
  color: #919aa3;
  justify-content: space-between;
  padding-top: 45px;
  padding-bottom: 45px;
  display: flex;
}

@media (max-width: 576px) {
  .footer-content-wrapper {
    text-align: center;
    display: block;
  }
}

.footer-content-wrapper a {
  color: inherit;
  transition: color .3s;
}

.footer-content-wrapper a:hover {
  color: #000;
  text-decoration: none;
}

.login-card .login-btn {
  color: #fff;
  background-color: #000;
  border-radius: 0;
  margin-bottom: 36px;
  padding: 13px 20px;
  font-size: 20px;
  font-weight: bold;
}

input[type="button"].btn-block, input[type="reset"].btn-block, input[type="submit"].btn-block {
  width: 100%;
}

.sr-only {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.custom-control-label {
  vertical-align: top;
  margin-bottom: 0;
  position: relative;
}

.custom-control {
  min-height: 1.5rem;
  padding-left: 1.5rem;
  display: block;
  position: relative;
}

